.header {
  background-image: url(../images/header-background.svg);
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: top right;

  @include onTablet {
    background-size: 55%;
  }

  &__content {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    // justify-content: space-between;

    // border: 1px solid red;

    // height: 100vh;
    min-height: 100vh;
    // max-height: 1000px;
    // margin: 0;
    padding: $headerTopPadding 0 40px;

    @include onDesktop {
      margin: 0;
    }

    @include onBigDesktop {
      margin: 0 $horisontalMarginHeader;
      // margin: 0;
    }
  }

  &__top {
    @include top-actions;
  }

  &__bottom {
    display: flex;
    flex-direction: column-reverse;
    // align-items: center;
    // margin: auto $horisontalMarginHeader;
    // margin: auto 0;

    @include onTablet {
      display: grid;
      margin: auto 0;
    }
  }

  &__bottom-content {
    display: flex;
    flex-direction: column;
    align-items: center;

    margin: 0 auto;

    // position: relative;
    // z-index: 1;
    // flex-grow: 1;
    // flex-shrink: 0;
    // min-width: 320px;
    max-width: 520px;
    // margin-right: 20px;

    // opacity: 0; // Початкова прозорість 0
    // transform: translateY(20px); // Початкове положення зсунуто вниз

    // transition: opacity 0.5s ease, transform 0.5s ease; // Плавна анімація

    @include onTablet {
      // margin-top: 20px;
      // margin: auto 0;
      align-items: start;
    }
  }

  &__sub-title {
    margin: 0 0 18px;
    padding: 0;
    font-weight: 600;
    line-height: 1.5;
    letter-spacing: 2px;
    text-transform: uppercase;
  }

  &__title {
    margin: 0 0 30px;
    padding: 0;
    letter-spacing: -0.5px;
    font-weight: 400;
    font-size: 34px;
    line-height: 100%;
    text-align: center;

    @include onBigMobile {
      font-size: 44px;
    }

    @include onTablet {
      font-size: 4rem;
      text-align: left;
    }
  }

  &__description {
    margin: 0 0 30px;
    line-height: 24px;
    letter-spacing: -0.5px;
    font-size: 1.125rem;
    text-align: center;

    @include onTablet {
      margin: 0 0 110px;
      text-align: left;
    }
  }

  &__animation-container {
    margin: 20px auto;
    width: 75%;
    // height: 45vh;

    @include onTablet {
      margin: 0;
      width: 100%;
    }
  }
}