.button {
  box-sizing: border-box;
  color: #0078db;
  cursor: pointer;
  z-index: 1;
  background-color: #0000;
  border: 2px solid #0078db;
  border-radius: 6px;
  width: max-content;
  padding: 14px 28px;
  font-weight: 700;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.button:before {
  content: "";
  z-index: -1;
  background-color: #0078db;
  width: 100%;
  height: 100%;
  transition: all .3s;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-100%);
}

.button:hover {
  color: #fff;
}

.button:hover:before {
  transform: translateX(0);
}

.page {
  color: #000;
  scroll-behavior: smooth;
  font-family: DM Sans, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 130%;
}

.page__body {
  margin: 0;
}

.page__body--with-menu {
  overflow: hidden;
}

.page__menu {
  transition: transform .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu:target {
  transform: translateX(0);
}

